import "../styles/styles.css";

const Header = () => (
    <header>
        <h1>Marvel Search</h1>
        <style jsx>{`
        h1 {
            margin-bottom: 20px;          
            background-image: url(http://i.annihil.us/u/prod/marvel/i/mg/9/20/5102c774ebae7.jpg)
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center;
        }    
        <div>$name - $props.name</div>
    `}</style>
    </header>
);

export default Header;